.NavBar {
    position: absolute;
    top: 200px;
    width: 100%;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    padding-left: 10%;
    padding-right: 10%;
    background: #191d2294;
}

.sticky {
    position: fixed;
    top: 0;
}